.p-cards{
    padding-top: rem(82);
    
}
.p-card{
    position: relative;
    display: flex;
    padding-bottom: rem(120);
    @include mq(md){
        flex-direction: column; 
        margin: 0 auto;
        max-width: rem(600);  
       }
}
