.p-card:nth-of-type(2){
    flex-direction: row-reverse;
    @include mq(md){
        flex-direction: column;   
       }
}


.p-card__img{
    height: auto;
    width: 50%;
    @include mq(md){
     width: 90%; 
     margin: 0 auto;  
    }
}

.p-card__figure{
    
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 66.66% ;
}
.p-card__figure img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.p-card__figure:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: $navy-grad;
    top: 9%;
    left: -6%;
    z-index: -1;
    @include mq(md){
        top: -9%;
    }
}
.p-card__figure-1st::before{
    position: absolute;
    font-family: $font-family-Shelby;
    color: $yellow;
    font-size: rem(60);
    bottom: -15%;
    right: 5%;
    content: "Web-Site";
    z-index: 100;
}
.p-card__figure-3rd::before{
    position: absolute;
    font-family: $font-family-Shelby;
    color: $yellow;
    font-size: rem(60);
    bottom: -15%;
    right: 5%;
    content: "Coding";
    z-index: 100;
}
.p-card__figure-2nd::before{
    position: absolute;
    font-family: $font-family-Shelby;
    color: $yellow;
    font-size: rem(60);
    bottom: -15%;
    left: 5%;
    content: "Design";
    z-index: 100;
}

.p-card__figure-2nd:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: $navy-grad;
    top: 9%;
    left: 6%;
    z-index: -1;
    @include mq(md){
        top: -9%;
    }
}
.p-card__texts{
padding-left: rem(60);
width: 50%;
color: $black;

@include mq(md){
    width: 100%;
    padding-left: 0; 
    text-align: center;  
   }
}

.p-card__texts-2nd{
    padding-left: 0;
    padding-right: rem(60);
    @include mq(md){
        padding-right: 0;
    }
}
.p-card__title{
    font-size: rem(36);
    line-height: rem(36);
    padding-top: rem(40);
    color: $black;
    font-weight: bold;
    // padding-top: rem(50);
    text-align: center;
    @include mq(md){
    text-align: center;
    padding-top: rem(30);
     font-size: rem(30);
    }
}
.p-card__text{
font-size: rem(18);
line-height: 2.5;
padding-top: rem(31);
@include mq(md){
    padding-left: rem(15);
    padding-right:rem(15);
    text-align: left;
}
}
.p-card__title-01{
  padding-left: rem(20);  
  position: relative;  
}
.p-card__title-01::after{
    position: absolute;
    font-family: $font-family-Shelby;
    color: $black;
    font-size: rem(48);
    top: 65%;
    left: 0;
    content: "01";
    @include mq(md){
        top: 60%;
        left: 5%;
        }
        @include mq(sm){
           left: 0; 
            }
}
