  max-width: 87.5rem;
  margin: 0 auto;
  padding-left: 7.5rem;
  padding-right: 7.5rem;
}
@media screen and (max-width: 767px) {
  .p-service__inner {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.p-service::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 0;
  border-style: solid;
  /*ここで三角形のサイズを決める。必ず幅100vwにして、ブラウザサイズいっぱいにしてください。*/
  border-width: 80px 100vw 0 0;
  /*transparentで余分な線を消す*/
  border-color: transparent #ffffff transparent transparent;
}

/*ContactForm7のテーブル化レスポンシブ調整*/
/*テーブルを親要素の幅いっぱいに伸ばす*/
.table-res-form {
  width: 100%;
}

/*一行ごとに上ボーダー*/
.table-res-form tr {
  border-top: 1px #DDD solid;
}

/*一番上の行だけボーダーなし*/
.table-res-form tr:first-child {
  border-top: none;
}

/*背景色つける*/
/*thとtd内の余白指定*/
.table-res-form th, .table-res-form td {
  padding: 1.5em;
}

/*th（項目欄）は30%幅、テキストを右寄せ*/
.table-res-form th {
  width: 30%;
  text-align: right;
}

/*「必須」部分のスタイリング*/
.table-res-form .requied {
  font-size: 0.8em;
  color: #FFF;
  display: inline-block;
  padding: 0.3em 0.3em 0.2em;
  background: red;
  border-radius: 2px;
}

/*ここから768px以下の指定*/
@media only screen and (max-width: 768px) {
  /*thとtdをブロック化して幅100％にして、縦積み*/
  .table-res-form th, .table-res-form td {
    width: 100%;
    display: block;
  }

  /*th（項目名）欄の不要な余白削除・テキスト左寄せ*/
  .table-res-form th {
    padding-bottom: 0;
    text-align: left;
  }

  /*不要な最終行の左のthを非表示にする*/
  .table-res-form .empty {
    display: none;
  }
}
/*ここまで768px以下の指定---終わり*/
/*ここから入力欄のスタイル指定*/
/*各入力フォームスタイリング*/
.table-res-form input[type=text], input[type=email], textarea {
  border: 1px #1D2737 solid;
  padding: 0.5em;
  border-radius: 5px;
  margin-bottom: 0.5em;
  width: 90%;
}
@media screen and (max-width: 767px) {
  .table-res-form input[type=text], input[type=email], textarea {
    width: 100%;
  }
}

/*入力欄にフォーカスしたら変化*/
.table-res-form input[type=text]:focus, input[type=email]:focus, textarea:focus {
  background: #EEFFFF;
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.5);
}

/*送信ボタンスタイリング*/
.table-res-form input[type=submit] {
  border: 1px #1D2737 solid;
  padding: 0.5em 4em;
  border-radius: 5px;
  background: #1D2737;
  color: #fff;
  box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.5);
  font-weight: bold;
}

/*送信ボタン・マウスオーバー時スタイリング*/
.table-res-form input[type=submit]:hover {
  background: #006599;
  box-shadow: none;
}

.p-works-wp {
  width: 90%;
  width: 100%;
  position: relative;
  padding-top: 8.75rem;
  padding-bottom: 8.75rem;
}
@media screen and (max-width: 767px) {
  .p-works-wp {
    padding-top: 6.25rem;
    padding-bottom: 4.375rem;
  }
}

.p-works-wp::before {
  position: absolute;
  width: 15vw;
  height: 30vw;
  top: -10vw;
  left: 0;
  content: "";
  background: url(../img/works-1.png) no-repeat center center/contain;
  overflow: hidden;
  z-index: 10;
}
@media screen and (max-width: 767px) {
  .p-works-wp::before {
    width: 25vw;
    height: 50vw;
    top: -20vw;
  }
}

.p-works-wp::after {
  position: absolute;
  width: 15vw;
  height: 7.5vw;
  bottom: 0;
  right: 0;
  content: "";
  background: url(../img/works-2.png) no-repeat center center/contain;
  overflow: hidden;
}
@media screen and (max-width: 767px) {
  .p-works-wp::after {
    width: 25vw;
    height: 12.5vw;
  }
}

.p-works-wp__block {
  margin: 0 auto;
  width: 90%;
  max-width: 67.5rem;
}

.p-works-wp__cards {
  padding-top: 3.125rem;
  display: flex;
}
@media screen and (max-width: 767px) {
  .p-works-wp__cards {
    display: block;
  }
}

.works-wp__cards {
  width: calc(50% - 20px);
  margin-left: 2.5rem;
  margin-bottom: 5.3125rem;
  background: #ffffff;
  padding-bottom: 1.875rem;
}
@media screen and (max-width: 767px) {
  .works-wp__cards {
    width: 100%;
    margin-left: 0.875rem;
    margin: 0 auto;
    margin-bottom: 3.7rem;
    padding: 0 0.9375rem;
  }
}

@media screen and (max-width: 767px) {
  .p-works-wp__card-item {
    margin-bottom: 3.125rem;
    margin-left: 0;
  }
}

.works-wp__cards:first-of-type {
  margin-left: 0;
}

.works-card__texts {
  text-align: center;
}

.p-works {
  width: 90%;
  max-width: 67.5rem;
  margin: 0 auto;
  position: relative;
  padding-top: 6.25rem;
}

.p-works__btn {
